import config from '@/config/config'
import { inherits } from 'util';

let loaded = 0;
let totalLen = '';

let Preload = function(percentageCB, loadedCB) {
    this.percentageCB = percentageCB;
    this.loadedCB = loadedCB;
}

Preload.prototype.init = function() {
    // console.log('preload')
    // debugger
    this.preloadImg();
    this.preloadAudio();
}

Preload.prototype.preloadImg = function() {
    // @/config/config.js
    window.imagesCache = [];
    totalLen = config.imgs.length;
    // console.log(config)
    let percentage = 0

    config.imgs.forEach( (v, i) => {
        let img = new Image();

        img.onerror = img.onload = () => {
            loaded++;

            percentage = loaded/totalLen

            // ioscanplaythrough
            // 
            this.percentageCB(percentage)
            // $emit('percentage', percentage);

            if(loaded >= totalLen) {
                // 
                // $emit('loaded', percentage);
                this.loadedCB(percentage)
            }
        }

        // require
        img.src = require('@/assets/' + v);
        window.imagesCache[i] = img;

    })
}

Preload.prototype.preloadAudio = function() {
    window.audioCache = [];
    config.audios.forEach( (v, i) => {
        let audio = new Audio();

        audio.addEventListener('canplaythrough', () => {
            audio.play();
            audio.pause();
        }, false)
        document.addEventListener('WeixinJSBridgeReady', () => {
            audio.play();
            audio.pause();
        }, false)

        audio.src = require('@/assets/' + v);
        window.audioCache[i] = audio;
    })

}

export default Preload



